<template>
    <div id="container">
        <v-card max-width="1200px" class="mx-auto mt-9">
                <v-card-title>
                    <h3>Cetak MO Besar</h3>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters  v-for="(i, index) in anak" :key="index">
                            <v-col cols="1">
                                <h3 class="mt-2"> Kode Barang</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-autocomplete dense single-line outlined :items="TblStok" item-text="Kode Barang" item-value="Kode Barang" label="Kode Barang" class="title mr-4 ml-2" v-model="i.kodeBarang">
                                    <template slot="item" slot-scope="data">
                                        {{ data.item["Kode Barang"] }} - {{ data.item["Nama Barang"] }}
                                    </template>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Jumlah</h3>
                            </v-col>
                            <v-col cols="1">
                                <v-text-field dense single-line outlined type="number" label="Jumlah" class="title mr-4" v-model="i.jumlah">
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">No.Bets</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="No Bets" class="title mr-4" v-model="i.noBet">
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Tanggal</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-menu ref="menu" v-model="i.menu" :close-on-content-click="false" :return-value.sync="date" transition="scale-transition" >
                                    <template v-slot:activator="{ on }">
                                        <v-text-field dense single-line outlined v-model="i.tanggal" label="Tanggal" class=" mr-4" 
                                            readonly v-on="on"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="i.tanggal" no-title scrollable>
                                        <v-spacer></v-spacer>
                                        <v-btn text color="primary" @click="i.menu = false">Cancel</v-btn>
                                        <v-btn text color="primary" @click="$refs.menu[index].save(date)">OK</v-btn>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>
                            <v-col cols="1">
                                <v-btn icon color="red" light @click="delAnak(index)">
                                    <v-icon>mdi-trash-can</v-icon>
                                </v-btn>
                            </v-col>
                        </v-row>
                        <v-btn depressed dark rounded style="text-transform: none !important;" color="green accent-3" @click="addDAnak">
                            <v-icon size="18" class="mr-2">mdi-plus-thick</v-icon> Tambah Item
                        </v-btn>
                        <v-btn class="ml-3" depressed dark rounded style="text-transform: none !important;" color="blue accent-3" @click="posting()">
                            <v-icon size="18" class="mr-2">mdi-check</v-icon>Cetak
                        </v-btn>
                    </v-container>
                </v-card-text>
            </v-card>
    </div>
</template>
<script>
export default {
    data() {
            return {
                TblStok: [],
                dataAtas: [],
                anak: [{
                    kodeBarang: '',
                    jumlah: 0,
                    noBet: '',
                    tanggal:'',
                }],
                date: new Date().toISOString().substr(0, 10),
                menu: false,
                datapost: new FormData,
                postData: [],
            }
        },
        methods: {
             posting(){
                var uri = this.$apiUrl + 'cetakmo/reset'
                this.$http.get(uri).then(response =>{
                 this.posData = []
                    for (var i in this.anak) {
                        this.postData.push({
                            'Kode Barang':this.anak[i]["kodeBarang"],
                            'No Bets':this.anak[i]["noBet"],
                            'Tanggal Produksi':this.anak[i]["tanggal"],
                            'Expired Date':(((new Date(this.anak[i]["tanggal"])).getFullYear() + 4 ).toString() + (this.anak[i]["tanggal"]).substring(4,10)),
                            'Jumlah': this.anak[i]["jumlah"],
                        })
                    }
                    let ob = {
                        anak: this.postData
                    }
                    var uri2 = this.$apiUrl + 'cetakmo/array'
                    this.$http.post(uri2, ob).then(response => {
                        
                        this.$router.push({ path: `/cetakmobesarprint` })
                    })
                this.resetForm();
                })                
            },

            ii(index) {
                this.anak.splice(index, 1)
            },

            addDAnak() {
                this.anak.push({
                    kodeBarang: '',
                    jumlah: 0,
                    noBet: '',
                    tanggal:'',
                    menu: false,
                })
            },

            delAnak(index){
                this.anak.splice(index,1);
            },

            getDataTblStok() {
                var uri = this.$apiUrl + 'tblstoks/barangjadi'
                this.$http.get(uri).then(response => {
                    this.TblStok = response.data.data
                })
            },

            resetForm() {
                this.anak = [{
                    kodeBarang: '',
                    jumlah: 0,
                    noBet: '',
                    tanggal:'',
                }]
            }
        },
        mounted() {
            this.getDataTblStok();
        },
}
</script>